


































































































































































































































































.container-scrollbar-thumb{
    &::-webkit-scrollbar-track-piece {
      background-color: #ffffff;
    }

    &::-webkit-scrollbar {
      width: 8px;
      height: 9px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #ebebeb;
      background-clip: padding-box;
      min-height: 28px;
      border-radius: 4px;
    }
}
