  position: relative;
  width: 100%;
  height: 100vh;
  padding: 0px;
  transition: width 0.2s linear;
  .idm-full-screen-layout-box{
    position: relative;
    height: 100%;
    width: 100%;
    cursor: crosshair;
    &.no-layout{
      cursor:inherit;
    }
    .fsl-bg-grid,.fsl-box-body{
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      >div{
        position: absolute;
      }
    }
    .fsl-bg-grid{
      z-index: 0;
      user-select: none;
      >.horizontal-line{
        left: 0;
        right: 0;
        border-bottom: 1px dashed #ddd;
      }
      >.vertical-line{
        top: 0;
        bottom: 0;
        border-left: 1px dashed #ddd;
      }
    }
    .fsl-box-body{
      z-index: 1;
      pointer-events: none;
      .fsl-region-element{
        border: 1px solid #1890ff;
        background-color: rgba($color: #1890ff, $alpha: 0.15);
        pointer-events:initial;
        cursor: pointer;
        overflow: hidden;
        .fsl-region-element-inner-develop,.fsl-region-element-inner-preview{
          position:absolute;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
        }
        .fsl-region-element-inner-develop{
          transition: all linear 0.3s;
          z-index: 2;
          pointer-events: none;
          *{
            pointer-events: initial;
          }
          &>*:not(.default-show){
            opacity: 0;
            transition: all linear 0.3s;
          }
          .fsl-element-number{
            position: absolute;
            left: 12px;
            top: 12px;
            border: 1px solid #1890ff;
            width: 24px;
            height: 24px;
            border-radius: 17px;
            text-align: center;
            line-height: 22px;
            font-size: 12px;
            color: #1890ff;
          }
          .fsl-element-where-font{
            position: absolute;
            left: 10px;
            right: 10px;
            text-align: center;
            top: 50%;
            margin-top: -6px;
            font-size: 12px;
            color: #188fff83;
          }
          .fsl-element-ctrl-center{
            position: absolute;
            width: 32px;
            height: 32px;
            border-radius: 16px;
            left: 50%;
            top: 50%;
            margin-left: -16px;
            margin-top: -16px;
            background-color: #1890ff;
            cursor: move;
            text-align: center;
            svg{
              font-size: 28px;
              max-height: 28px;
              width: 28px;
              margin-top: 2px;
              fill: currentColor;
              vertical-align: middle;
              color: #ffffff;
            }
          }
          .fsl-element-item-delete{
            position: absolute;
            right: 10px;
            top: 10px;
            svg{
              font-size: 20px;
              max-height: 20px;
              width: 20px;
              fill: currentColor;
              vertical-align: middle;
              color: #1890ff;
            }
            transition: all linear 0.3s;
            &:hover{
            opacity: 1;
            }
          }
          .fsl-element-ctrl-anchor{
            position: absolute;
            width: 10px;
            height: 10px;
            background-color: #1890ff;
            &.fsl-element-ctrl-topleft{
              top: 0;
              left: 0;
              cursor: nw-resize;
            }
            &.fsl-element-ctrl-top{
              top: 0;
              left: 50%;
              margin-left: -5px;
              cursor: n-resize;
            }
            &.fsl-element-ctrl-topright{
              top: 0;
              right: 0;
              cursor: ne-resize;
            }
            &.fsl-element-ctrl-left{
              top: 50%;
              left: 0;
              margin-top: -5px;
              cursor: w-resize;
            }
            &.fsl-element-ctrl-right{
              top: 50%;
              right: 0;
              margin-top: -5px;
              cursor: e-resize;
            }
            &.fsl-element-ctrl-bottomleft{
              bottom: 0;
              left: 0;
              cursor: sw-resize;
            }
            &.fsl-element-ctrl-bottom{
              bottom: 0;
              left: 50%;
              margin-left: -5px;
              cursor: s-resize;
            }
            &.fsl-element-ctrl-bottomright{
              bottom: 0;
              right: 0;
              cursor: se-resize;
            }
          }
        }
        .fsl-region-element-inner-preview{
          z-index: 1;
          &::-webkit-scrollbar-track-piece {
            background-color: #ffffff;
          }

          &::-webkit-scrollbar {
            width: 8px;
            height: 9px;
          }

          &::-webkit-scrollbar-thumb {
            background-color: #ebebeb;
            background-clip: padding-box;
            min-height: 28px;
            border-radius: 4px;
          }
        }
        &:not(.layout-mode-common,.module_env_production){
          &:hover,&.active{
            background-color: rgba($color: #1890ff, $alpha: 0.3);
            .fsl-region-element-inner-develop{
              *{
                opacity: 1;
                &.fsl-element-item-delete{
                  opacity: 0.7;
                }
              }
            }
          }
          .drag_container_empty{
            background: transparent;
            color: transparent;
            outline-color: transparent;
            height: 100% !important;
            &:before{
              content: none;
            }
          }
          .fsl-region-element-inner-preview{
            opacity: 0.1;
          }
        }
        &.layout-mode-common{
          border:none;
          background: none;
          cursor: inherit;
          .drag_container_empty{
            height: 100% !important;
          }
          .fsl-region-element-inner-preview{
            &:not(.drag_container_empty){
              border: 1px dashed #1890ff;
              background-color: rgba($color: #1890ff, $alpha: 0.1);
            }
          }
          &.module_env_production{
            .fsl-region-element-inner-preview{
              &:not(.drag_container_empty){
                border: none;
                background: none;
              }
            }
          }
        }
      }
    }
    .fsl-region-element-inner-display{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      overflow: auto;
      z-index: 988;
      background-color: #FFFFFF;
    }
  }
  .fsl-layout-switch-tool,.fsl-layout-footer-switch-tool{
    position: absolute;
    cursor: pointer;
    height: 24px;
    border: 1px solid #1890ff;
    line-height: 22px;
    display: flex;
    >div{
      padding: 0px 10px;
      color: #1890ff;
      &.active{
        background-color: #1890ff;
        color: #ffffff;
      }
    }
  }
  .fsl-layout-switch-tool{
    z-index: 999;
    top: 0px;
  }
  .fsl-layout-footer-switch-tool{
    bottom: 0;
    z-index: 999;
    >div{
      border-right: 1px solid #1890ff;
      font-size: 12px;
      color: #666;
      i,svg{
        color: #1890ff;
      }
      &.active{
        color: #ffffff;
        i,svg{
          color: #ffffff;
        }
      }
      &:last-child{
        border-right: none;
      }
      input{
        border: none;
        outline: none;
        height: 22px;
        line-height: 22px;
        width: 100px;
        color: #666;
        margin: 0px 10px;
      }
    }
  }
  .fsl-mouse-region-element{
    position: absolute;
    border: 1px solid #1890ff;
    background-color: rgba($color: #1890ff, $alpha: 0.15);
  }
  
}
