.idm_lc_desc,
.idm_lc_addnewbtn {
  display: flex;
  align-items: center;
  cursor: pointer;
  .idm_lc_svgicon {
    fill: currentColor;
    vertical-align: middle;
  }
}
.idm_loopcontainer_box,
.idm_loopcontainer_msgbox {
  width: 100%;

  &.layout-horizontal {
    display: table;
    // align-items: center;
    .idm_loopcontainer_label_box {
      display: table-cell;
      padding-right: 16px;
      text-align: right;
      width: 88px;
      padding-right: 16px;
      padding-top: 0;
      vertical-align: middle;
    }
  }
  &.layout-vertical {
    .idm_loopcontainer_label_box {
      width: 100%;
      display: block;
      text-align: left;
      padding-top: 0;
      padding-bottom: 4px;
    }
    .idm_module_inner_box {
      width: 100%;
    }
  }
  .required {
    color: red;
  }
}
.idm_loopcontainer_box {
  .table-field-layout-table {
    display: table;
    margin-bottom: 16px;
    min-height: 84px;
    position: relative;
    table-layout: fixed;
    width: 100%;
    .table-field-table-head {
      background-color: rgba(31, 56, 88, 0.06);
      color: rgba(0, 0, 0, 0.6);
      display: table-row;
      font-size: 12px;
      font-weight: 700;
      height: 30px;
      line-height: 30px;
      overflow: hidden;
      text-align: left;
      .table-field-table-cell {
        display: table-cell;
        padding: 10px 8px;
      }
    }
    .table-field-table-row {
      display: table-row;
      font-size: 12px;
      overflow: hidden;
      text-align: left;
      .table-field-table-cell {
        display: table-cell;
        padding: 10px 8px;
        vertical-align: middle;
        &.item-idx {
          text-align: center;
        }
      }
    }
    .table-field-table-empty {
      color: #999;
      font-size: 13px;
      height: 50px;
      line-height: 50px;
      position: absolute;
      text-align: center;
      width: 100%;
    }
    &.table-field-theme-zebra .table-field-table-row:nth-child(odd) {
      background-color: rgba(31, 56, 88, 0.04);
    }
    &.table-field-theme-split .table-field-table-cell {
      border-bottom: 1px solid #e8e8e8;
    }
    &.table-field-theme-border {
      border-collapse: collapse;
      .table-field-table-cell {
        border: 1px solid #e8e8e8;
      }
    }
  }
}
